@import './utils.scss';


.SignUp {

    margin-top: 50px;
    width: 35%;
    height: 500px;
    
    .SignUpWrapper {
        display: flex;
        align-items: center;
        flex-direction: column;
        
        width: 100%;
        height: 100%;
        
        #title {
            font-size: 24px;
            margin-bottom: 20px;
        }
        
        .Contents {
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
            
            margin-bottom: 30px;
            

            input {
                border: none;
                outline: none;    
                height: 39px;
                border-radius: 30px;
                background-color: $BaseBackground;
                text-align: center;
            }
    
            button{
                border: none;
                outline: none;
                height: 39px;
                border-radius: 30px;
                cursor: pointer;
                
            }
    
            .EmailRequestWrapper {
                display: flex;
                flex-direction: row;
                width: 100%;
                
                input {
                    width: 60%;
                }

                button{
                    width: 40%;
                    margin-left: 5px;
                    background-color: #FFE69A;
                }
            }
    
            .EmailConfirmWrapper {
                display: flex;
                flex-direction: row;
                width: 100%;
                input {
                    width: 60%;
                }

                button{
                    width: 40%;
                    margin-left: 5px;
                    background-color: #FFE69A;
                }
                
            }
            .NickNameAndRegionWrapper{
                display: flex;
                flex-direction: row;
                width: 100%;

                #nickname{
                    width: 50%;
                }

                #region{
                    margin-left: 30px;
                    width: 50%;
                }
            }

            .dropbtn {  
                background-color: $BaseBackground;
                color: #a9a9a9;
                line-height: 37px;
                border: none;
              }
              
              .dropdown {
                position: relative;
                
                width: 100%;
                display: inline-block;
              }
              
              .dropdown-content {
                display: none;
                height: 45px;
                overflow-y: scroll;

                position: absolute;
                background-color: #f1f1f1;
                min-width: 160px;
                box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
                z-index: 1;
              }
              
              .dropdown-content div {
                color: black;
                cursor: pointer;
                padding: 12px 16px;
                text-decoration: none;
                display: block;
              }
              
              .dropdown-content div:hover {background-color: #ddd;}
              
              .dropdown:hover .dropdown-content {display: block;}
              
              .dropdown:hover .dropbtn {background-color: $PointBackground;}
            
        }
        input {
            width: 100%;
        }
        button{
            width: 100%;
            height: 50px;
            border: none;
            border-radius: 30px;
            outline: none;
            background-color: $PointBackground;
            cursor: pointer;
        }
    }

}